import React from 'react'
import {Link} from 'react-router-dom'
import Breadcrumps from './Breadcrumps.js';

class OrderDone extends React.Component{
    constructor(props){
      super(props);
      switch(props.data.paymentType){
        case 'offlineCard':
          this.paymentType = '–ö–∞—Ä—Ç–æ–π –∫—É—Ä—å–µ—Ä—É';
          break;
        case 'onlineCard':
          this.paymentType = '–ö–∞—Ä—Ç–æ–π –æ–Ω–ª–∞–π–Ω';
          break;
        case 'offlineCash':
          this.paymentType = '–ù–∞–ª–∏—á–Ω—ã–º–∏ –∫—É—Ä—å–µ—Ä—É';
          break;
        default:
          this.paymentType = '–ù–µ –∏–∑–≤–µ—Å–Ω–æ'
      }
      this.state = {
        paths:[{path:'/',name:'–ì–ª–∞–≤–Ω–∞—è'},{path:'/order',name:'–ö–æ—Ä–∑–∏–Ω–∞'},{path:'/order',name:'–û—Ñ–æ—Ä–º–ª–µ–Ω–∏–µ –∑–∞–∫–∞–∑–∞'},{path:'/order',name:'–ó–∞–∫–∞–∑ –ø—Ä–∏–Ω—è—Ç'}]
      }
    }
  
    render(){
      const data = this.props.data;
      return(      
      <div className="wrapper order-wrapper">
        <Breadcrumps paths={this.state.paths}/>                  
          <section className="order-done">
            <h2 className="order-done__title order-process__title">–ó–∞–∫–∞–∑ –ø—Ä–∏–Ω—è—Ç, —Å–ø–∞—Å–∏–±–æ!</h2>
            <div className="order-done__information order-info">
              <div className="order-info__item order-info__item_summ"> 
                <h3>–°—É–º–º–∞ –∑–∞–∫–∞–∑–∞:</h3>
                <p>{data.price} <i className="fa fa-rub" aria-hidden="true"></i></p>
              </div>
              <div className="order-info__item order-info__item_pay-form"> 
                <h3>–°–ø–æ—Å–æ–± –æ–ø–ª–∞—Ç—ã:</h3>
                <p>{this.paymentType}</p>
              </div>
              <div className="order-info__item order-info__item_customer-name"> 
                <h3>–ò–º—è –∫–ª–∏–µ–Ω—Ç–∞:</h3>
                <p>{data.name}</p>
              </div>
              <div className="order-info__item order-info__item_adress">
                <h3>–ê–¥—Ä–µ—Å –¥–æ—Å—Ç–∞–≤–∫–∏:</h3>
                <p>{data.address}</p>
              </div>
              <div className="order-info__item order-info__item_phone">
                <h3>–¢–µ–ª–µ—Ñ–æ–Ω:</h3>
                <p>{data.phone}</p>
              </div>
            </div>
            <p className={`order-done__notice ${this.email ? '' : 'hidden'}`}>–î–∞–Ω–Ω—ã–µ –æ –∑–∞–∫–∞–∑–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã –Ω–∞ –∞–¥—Ä–µ—Å <span>{this.email}</span></p>
            <Link to='/catalogue' className="order-done__continue" >–ø—Ä–æ–¥–æ–ª–∂–∏—Ç—å –ø–æ–∫—É–ø–∫–∏</Link>
          </section>
        </div>);
    }
  }

export default OrderDone


